@import './scss/functions';

:root {
    --first-color: #010409;
    --second-color: #0d1118;
    --navbar-color: #161b22;
    --btn-color: #21262d;
    --grey-color: #c9d1d9;
    --gray-two: #8b949e;
    --line-color: #30363d;
    --input-color: #0d1117;
    --border-color: #30363d;
    --text-blue-color: #58a6ff;
    --white: #ffffff;
}



* {
    margin: 0;
    box-sizing: border-box;
    padding: 0;
    list-style: none;
    text-decoration: none;
}

body {
    background-color: var(--first-color);
    color: var(--white);

}

input {
    border-radius: 4px;
}
